<template>
    <div class="list">
      <el-main>
        <el-table
          :data="tableData"
          stripe
          style="width: 100%"
          @selection-change="selectionChange"
        >
          <el-table-column
            prop="select"
            label=""
            width="50"
            show-overflow-tooltip
            type="selection"
          >
          </el-table-column>
          <el-table-column
            prop=""
            label="#"
            type="index"
            :index="formatter"
            width="50"
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column
            prop="userName"
            label="用户名"
            width="150"
            show-overflow-tooltip
          >
          </el-table-column>
          <el-table-column
            prop='examine'
            label="状态"
            width="150"
            show-overflow-tooltip
          >
            <template slot-scope="scope">
              <el-tag type="success" v-if="examine == 1">通过</el-tag>
              <el-tag type="danger" v-if="examine == 2">未通过</el-tag>
              <el-tag type="info" v-if="examine == 0">未审核</el-tag>
            </template>
          </el-table-column>
          <el-table-column
            prop="createTime"
            label="时间"
            width="180">
          </el-table-column>
          <el-table-column
            prop="reason"
            label="理由"
            width="200">
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                @click="edit(scope.row._id); FormVisible = true">查看</el-button>
              <!-- Form   添加-->
              <el-dialog title="审核" :visible.sync="FormVisible">
                <el-form :model="formData">
                  <el-form-item label="">
                      <img :src="formData.img" class="avatar">
                  </el-form-item>
                  <el-form-item>
                  <el-radio v-model="formData.examine" label="1">通过</el-radio>
                  <el-radio v-model="formData.examine" label="2">不通过</el-radio>
                  <el-input
                    type="textarea"
                    autosize
                    placeholder="请输入理由"
                    v-model="formData.reason">
                  </el-input>
                  </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                  <el-button type="primary" size="mini" @click="updateSummary">提交</el-button>
                </div>
              </el-dialog>
              <el-button
                size="mini"
                type="danger"
                @click="del(scope.row._id)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-main>
      <el-footer>
        <el-button type="primary"  @click="toggleSelection(multiSelection)">批量重置密码</el-button>
        <el-pagination
          background
          layout="prev, pager, next,jumper"
          :total="total"
          :current-page="page"
          :page-size="pageSize"
          @current-change="pageChange"
        >
        </el-pagination>
      </el-footer>
    </div>
</template>
<script>
    export default{
      name: "",
      data(){
          return{
            tableData:[],
            //弹框
            FormVisible: false,
            formData:{
              img:'',
              examine:"",
              reason:''
            },
            //选择器
            multiSelection:[],
            userId:[],
            //分页器
            pageSize:5,
            page:1,
            total:10
          }
      },
      methods:{
          //获取心得总结
        getSummary(){
            this.$axios.get('getReports', {type:1}).then( res => {
                this.tableData = res.data
            })
        },
        //查看，审核
        edit(id){
          this.$router.push({path:'/summary',query:{id} })
          //数据回填
          this.$axios.get('getReports',{id:this.$route.query.id}).then(res => {
            this.formData = res.data
          })
        },
        //修改 审核
        updateSummary(){
          let params = {
            ...this.formData,
            id:this.$route.query.id
          }
          this.$axios.post('updateReports',params).then( res => {
            if(res.code == 200){
              this.$message({
                message:`${res.msg}`,
                type:'success'
              })
            }else{
              this.$message({
                message:`${res.msg}`,
                type:'warning'
              })
            }
            //关闭弹框
            this.FormVisible= false;
            setTimeout( () => {
              this.$router.push('/summary')
              this.$router.go(0)
            },1000)
          })
        },
        del(id){
          this.$confirm('此操作将永久删除该内容, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.$axios.post('delReports', {id}).then(res => {
              if(res.code == 200){
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                });
                setTimeout( () => {
                  this.$router.go(0)
                },1000)
              }else{
                this.$message({
                  type: 'error',
                  message: '删除失败!'
                });
              }
            })
          }).catch(() => {
            this.$message({
              type: 'warning',
              message: '已取消删除'
            });
          })
        },
        //批量选择
        selectionChange(val){
          this.multiSelection = val
//          console.log(val)
        },
        toggleSelection(rows){
          if(rows){
            for(var key in rows){
//                  console.log(key)
//                this.userId = rows[key]._id
              this.userId.push(rows[key]._id)
            }
//            console.log(this.userId)
            this.$axios.post('updateManyPwd', {userId:this.userId}).then( res => {
              if(res.code == 200) {
                this.$message({
                  type:'success',
                  message:'批量修改成功'
                })
              }
            })
          }else {
            this.$message({
              type:'warning',
              message:'没有选择'
            })
          }
        },
        //底部分页器
        //这个时候利用formatter就可以实现索引累加啦
        formatter(index){
          //放回索引
          return index + (this.page -1) *this.pageSize +1
        },
        pageChange(val){
//          console.log(`当前页: ${val}`);
          this.page = val
          this.$axios.get('getReports',{page:val, pageSize:this.pageSize,type:1}).then( res => {
//              setTimeout( () => {
//                this.$router.go(0)
//              },1000)
            this.tableData = res.data
          })
        },

        //获取数据库文档总数
        getNumber(){
          this.$axios.get('getReportsNum',{type:1}).then( res => {
            this.total = res.data
          })
        }
      },
      created(){
          this.getSummary();
          this.getNumber()
      }
    }
</script>
<style scope>
  .list {
    width: 100%;
    padding-top: 2px;
    padding-left: 20px;
  }
  /*内容区*/
  .list .el-main{
    margin:10px 22px 10px 0;
    padding: 0 14px;
    border:1px solid #dfe7ed;
  }
  .list .el-table .has-gutter .cell{
    color:#000;
  }
  .list .avatar {
    display: block;
    width: 190px;
    height: 190px;
    margin: 0 auto;
  }
  /*底部*/
  .list .el-footer{
    /*position: fixed;*/
    /*min-height: 500px;*/
    /*bottom: 20px;*/
    /*right:20px;*/
    position:relative;
    height: 56px !important;
    margin:0 22px 0 0;
    padding:0;
    background-color: #f1f1f1 !important;
  }
  .list .el-button{
    margin:10px 0 0 10px;
  }
  .list .el-footer .el-pagination{
    display: inline-block;
    position:absolute;
    top:12px;
    right:15px;
  }
  .list .el-footer .el-pagination.is-background .btn-next, .el-pagination.is-background .btn-prev, .el-pagination.is-background .el-pager li{
    background-color: #ffffff;
  }
</style>



